import { useEffect, useState } from "react";
import SockJS from "sockjs-client";
import { Client } from "@stomp/stompjs";
import "./App.css";

function App() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [username, setUsername] = useState("");
  const [stompClient, setStompClient] = useState(null);

  useEffect(() => {
    const socket = new SockJS("http://182.31.165.82:8080/chat");

    const client = new Client({
      webSocketFactory: () => socket,
      debug: (str) => console.log(str),
      onConnect: () => {
        console.log("✅ WebSocket 연결 성공");
        setStompClient(client);

        client.subscribe("/topic/messages", (msg) => {
          const received = JSON.parse(msg.body);
          setMessages((prev) => [...prev, received]);
        });
      },
      onStompError: (frame) => {
        console.error("❌ STOMP 에러:", frame);
      },
    });

    client.activate();
    return () => client.deactivate();
  }, []);

  const sendMessage = () => {
    if (stompClient && stompClient.connected && input.trim() !== "") {
      const chatMessage = { sender: username || "익명", content: input };
      stompClient.publish({
        destination: "/app/send",
        body: JSON.stringify(chatMessage),
      });
      setInput("");
    } else {
      console.warn("⚠️ 아직 서버와 연결되지 않았습니다.");
    }
  };

  // 메시지 삭제
  const deleteMessage = (index) => {
    if (window.confirm("메시지를 삭제하시겠습니까?")) {
      setMessages(messages.filter((_, i) => i !== index));
    }
  };

  return (
    <div className="chat-container">
      <h2>실시간 채팅</h2>
      <hr />
      <input
        className="username-input"
        type="text"
        placeholder="이름 입력"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      />

      <div className="chat-messages">
        {messages.map((m, i) => (
          <div key={i} className="message">
            <div className="bubble">
              <span className="sender-name">{m.sender}</span>
              <div className="content-line">
                <span>{m.content}</span>
                {m.sender === (username || "익명") && (
                  <button
                    className="delete-btn"
                    onClick={() => deleteMessage(i)}>
                    🗑️
                  </button>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>

      <div className="chat-input">
        <input
          type="text"
          placeholder="메시지 입력"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === "Enter" && sendMessage()}
        />
        <button onClick={sendMessage}>보내기</button>
      </div>
    </div>
  );
}

export default App;
